import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";

import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Verify = () => {
  const [email, setemail] = useState('');

  const [data, setData] = useState('');


  const navigate = useNavigate();


  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get('/dashboard')
        if (response.data.user === "error") {
          return

        } else {
          navigate('/userdashboard', { replace: true })

        }

      } catch (error) {
        console.error(error);
      }

    }
    getUser()


  }


    , [])






  var getdata = "Please Enter Email and password";

  let submit = async (e) => {
    e.preventDefault()
    toast('submited', {
      position: "top-center"
    })

  

  try {
    await axios.post('/verification', {
      email: email,

    }
    ).then(function (response) {
      getdata = response.data.message;
      setData(getdata);
      toast(getdata, {
        position: "top-center"
      })



    })
      .catch(function (error) {
        toast.error('Something is wrong!', {
          position: "top-center"})
     
      
      });

  } catch (error) {
    toast.error('Something is wrong!', {
      position: "top-center"})
 
  }

}


return (
  <>
    <form method='post'>
      <h1>Type your email</h1>
      <div className="form-outline mb-4">
        <label className="form-label" for="form2Example1">E-mail</label>
        <input type="email" name='email' onChange={(e) => { setemail(e.target.value) }} id="form2Example1" className="form-control" />

      </div>

      <button type="submit" onClick={submit} style={{ width: "358px" }} value={'Reset'} class="btn btn-primary btn-block mb-4">Reset</button>
    </form>
    <h2>{data}</h2>

<ToastContainer/>
  </>
)
}

export default Verify