import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import {QueryClient, QueryClientProvider} from 'react-query'
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Dashboard from './component/basics/Dashboard';
import Forget from './component/basics/forget';
import Verify from './component/basics/verify';
import Checkemail from './component/basics/checkemail';
import Forgetpassword from './component/basics/Forget-password';
import Editprofile from './component/basics/editprofile';
import Usersview from './component/basics/usersview';
import Profile from './component/basics/profile';
import Followers from './component/basics/followers';
import Following from './component/basics/following';
import Uploadblogs from './component/blogpost/uploadblogs';
import Myblogs from './component/blogpost/myblogs';
import Editblog from './component/blogpost/editblog';
import Blogs from './component/blogpost/blogs';
import Followingpost from './component/blogpost/followingpost';
import Chat from './component/chat/Chat';
import Test from './component/Test';
import Resgister from './component/basics/Resgister'
import Reward from './component/reward/Reward';
import Blogview from './component/blogpost/Blogview';
import Searchusers from './component/basics/searchusers';
import Searchblogs from './component/blogpost/searchblogs';
import UploadTextpost from './component/textpost/uploadtextpost';
import Mytextpost from './component/textpost/Mytextpost';
import Textposts from './component/textpost/textposts';
import Textpostview from './component/textpost/textpostview';
import Download from './component/blogpost/download';
import Downloadtpost from './component/textpost/downloadtpost';
import Home from './component/basics/Home';
import ChatbotComponent from './component/chatbot/chatbot';
// import { ReactQueryDevtools } from 'react-query/devtools'
import { ServiceWorker } from './serviceworker';
const queryClient = new QueryClient()
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <BrowserRouter>
            <QueryClientProvider client={queryClient}>

      <Routes>
        <Route path="/" element={<App />} />
        <Route path="/forget" element={<Forget />} />
        <Route path="/forget-password/:token" element={<Forgetpassword />} />
        <Route path="/edit" element={<Editprofile />} />
        <Route path="/resgister" element={<Resgister />} />

        <Route path="/usersview" element={<Usersview />} />
        <Route path="/profile/:id" element={<Profile />} />
        <Route path='/followers/:id' element={<Followers />} />
        <Route path='/following/:id' element={<Following />} />
        <Route path="/verify" element={<Verify />} />
        <Route path="/checkmail/:id" element={<Checkemail />} />
        <Route path="/test" element={<Test />} />
        <Route path="/reward" element={<Reward />} />
        <Route path="/searchusers" element={<Searchusers />} />
        <Route path="/searchblogs" element={<Searchblogs />} />

        <Route path="/userdashboard" element={<Dashboard />} />
        {/* blogs routers */}
        <Route path="/blogview/:id" element={<Blogview />} />

        <Route path="/uploadblog" element={<Uploadblogs />} />
        <Route path="/myblogs" element={<Myblogs />} />
        <Route path="/editblog/:id" element={<Editblog />} />
        <Route path="/savechat/:id" element={<Chat />} />

        <Route path="/blogs" element={<Blogs />} />
        <Route path="/home" element={<Home />} />

        <Route path="/followingposts" element={<Followingpost />} />

        {/* text post routes */}
        <Route path="/uploadtpost" element={<UploadTextpost />} />
        <Route path="/mytextpost" element={<Mytextpost />} />
        <Route path="/textposts" element={<Textposts />} />
        <Route path='/textpostview/:id' element={<Textpostview/>}/>
        <Route path='/download/:id' element={<Download/>}/>
        <Route path='/downloadtextpost/:id' element={<Downloadtpost/>}/>
      </Routes>
      {/* <ReactQueryDevtools initialIsOpen /> */}
<ChatbotComponent/>
              </QueryClientProvider>

    </BrowserRouter>
  </React.StrictMode>
);
// SwDevs()
ServiceWorker()