import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate, Link } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Mytextpost = () => {
    const [data, setData] = useState([]);
    const navigate = useNavigate();
    const [Loading, setloading] = useState(true)

    // login check karta hai
    useEffect(() => {
        async function getUser() {
            try {
                const response = await axios.get('/dashboard')
                console.log(response.data.user);
                if (response.data.user === "error") {
                    navigate('/', { replace: true })


                }

            } catch (error) {
                console.error(error);
            }

        }
        getUser()


    }


        , [])




        useEffect(() => {
            window.addEventListener("offline", (e) => {
              toast.info('you are offline.', {
                position: "bottom-center"
            })
            
            });
            
            window.addEventListener("online", (e) => {
              toast.info('you are online.', {
                position: "bottom-center"
            })
        
            });
            
          },[])
  

    useEffect(() => {
        async function getUser() {
            try {
                const response = await axios.get(`/mytextpost`)
                // console.log(response.data.post._id);
                setData(response.data.post)
                setloading(false)
            } catch (error) {
                console.error(error);
            }

        }
        getUser()


    }


        , [])
    const deletetextpost = async (blog) => {
        // console.log(blog)

        try {
            const response = await axios.get(`/mytextpostdelete/${blog}`)
            // console.log(response.data.post);
            // setData(response.data.post)
            toast.success('Blog delete Successfully', {
                position: "bottom-center"
            })
        } catch (error) {
            toast.error('Something is Wrong!', {
                position: "bottom-center"
            })
        }


    }
    const Isloading = () => {
        return (
            <>
                <div class="d-flex justify-content-center">
                    <div class="spinner-border text-primary" role="status">
                        <span class="visually-hidden">Loading...</span>
                    </div>
                </div>
            </>
        )
    }
    const Datanull = () => {
        return (
            <h1>Not Found</h1>
        )
    }
    // console.log(data.length)
    const Data = () => {
        return (<>
            <div style={{ marginTop: '80px' }}>
                {data.length === 0 ? (<Datanull />) : (
                    <div className="App">
                        <Link to={`/home`} style={{ textDecoration: 'none' }} activeClassName="current">
                            <i className='fa fa-arrow-left' style={{ fontSize: '30px' }}></i>
                        </Link>

                        {data.map((value) => (
                            <div key={value._id}>
                                <div className="card border-primary mb-3" style={{ border: "2px solid black", borderRadius: '8px' }}>
                                    <Link to={`/textpostview/${value._id}`} activeClassName="current">

                                        <div className="card-header text-bg-info">View</div>
                                    </Link>
                                    <div className="card-body">
                                        <p className="card-text">{value.question}</p>
                                        <button className='btn btn-danger' onClick={() => { deletetextpost(value._id) }}>Delete</button>

                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>)
                }
            </div>









        </>












        )

    }

    return (
        <>
            {Loading ? (<Isloading />) : (<Data />)}
            <ToastContainer />
        </>
    )
}

export default Mytextpost