import React from 'react'
import axios from 'axios'
import { useQuery } from 'react-query'
import { useNavigate, Link } from "react-router-dom";


const Test = () => {
  const navigate = useNavigate();

  const { isLoading, error, data, isError } = useQuery('test', async()=> await axios.get('/dashboard')
  )
  
  // console.log(data.user.name)
  const Isloading = () => {
    return (
      <>
        <div class="d-flex justify-content-center">
          <div class="spinner-border text-primary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>
      </>
    )
  }
  const Datas = () => {
    return (
      <>

        <div>
          <h1>Hello {data.data.user.name}</h1>
          <a href='/edit'>Update profile</a><br />
          <a href='/usersview'>other users</a>
          <Link to={`/following/${data.data.user._id}`} activeClassName="current"><h2>{data.data.user.following ? data.data.user.following.length : "0"} following</h2></Link>
          <Link to={`/followers/${data.data.user._id}`} activeClassName="current"><h2>{data.data.user.followers ? data.data.user.followers.length : "0"} followers</h2></Link>
          <Link to={`/uploadblog`} activeClassName="current"><button>Upload blogs</button></Link>
          <Link to={`/myblogs`} activeClassName="current"><button>My blogs</button></Link>
          <Link to={`/blogs`} activeClassName="current"><button>Blogs</button></Link>
          <Link to={`/followingposts`} activeClassName="current"><button>Following Posts</button></Link>
        </div>
      </>
    )
  }
  return (
    <>
      {isLoading ? <Isloading /> : isError ? (<h1>Errr</h1>): <Datas />}
    </>
  )
}

export default Test
