import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate, Link, useParams } from "react-router-dom";
import '../style/editblog.css';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Editblog = () => {

    const [data, setData] = useState({});
    const navigate = useNavigate();
    const verifyid = useParams('');
    const [imgurl, setimgurl] = useState('')
    const [Loading, setloading] = useState(true)
    const [Loadingimg, setloadingimg] = useState(false)
    const [load, setload] = useState(false)


    useEffect(() => {
        async function getUser() {
            try {
                const response = await axios.get('/dashboard')
                console.log(response.data.user);
                if (response.data.user === "error") {
                    navigate('/', { replace: true })

                }
                // setData(response.data.user)

            } catch (error) {
                console.error(error);
                navigate('/', { replace: true, })

            }

        }
        getUser()


    }


        , [])




        useEffect(()=>{
            window.addEventListener("offline", (e) => {
              toast.info('you are offline.', {
                position: "bottom-center"
            })
            
            });
            
            window.addEventListener("online", (e) => {
              toast.info('you are online.', {
                position: "bottom-center"
            })
        
            });
            
          },[])

          
    useEffect(() => {
        async function getUser() {
            try {
                const response = await axios.get(`/editblog/${verifyid.id}`)
                console.log(response.data.post._id);
                setData(response.data.post)
                setloading(false)
                setload(false)
            } catch (error) {
                toast.error('Something is wrong', {
                    position: "top-center"
                })
            }

        }
        getUser()


    }


        , [load])
    const loadFile = function (e) {
        var reader = new FileReader();
        reader.onload = function () {
            var output = document.getElementById('output');
            output.src = reader.result;
        };
        reader.readAsDataURL(e.target.files[0]);
    };

    const upadteimag = async (imageurl) => {
        try {
            await axios.post(`/editblog/${verifyid.id}`, {
                image: imageurl,
                user_id: data._id

            }
            )
        } catch (error) {
            toast.error('Something is wrong', {
                position: "top-center"
            })
        }
    }


    const okay = async () => {
        setloadingimg(true)
        try {
            const data = new FormData();
            data.append("file", imguser)
            data.append("upload_preset", "Arinsta")
            data.append("cloud_title", "arcloud555")


            fetch('https://api.cloudinary.com/v1_1/arcloud555/image/upload', {
                method: "post",
                body: data,

            }).then((res) => res.json())
                .then(data => {
                    upadteimag(data.url)
                    setloadingimg(false)
                    toast.success('blog image update sucessfully', {
                        position: "top-center"
                    })
                    setload(true)
                })
                .catch(err => {
                    toast.error('Something is wrong', {
                        position: "top-center"
                    })
                })

        } catch (error) {
            console.log(error)
        }
    }

    const [title, settitle] = useState('');
    const [description, setdescription] = useState('');

    const [imguser, setimguser] = useState('');






    const titlesubmit = async () => {
        try {
            await axios.post(`/editblog/${verifyid.id}`, {
                title: title,
                user_id: data._id

            }
            )
            setload(true)
            toast.success('title update sucessfully', {
                position: "top-center"
            })
        } catch (error) {
            toast.error('Something is wrong', {
                position: "top-center"
            })
        }
    }
    const dessubmit = async () => {
        try {
            await axios.post(`/editblog/${verifyid.id}`, {
                description: description,
                user_id: data._id

            }
            )
            toast.success('description sucessfully', {
                position: "top-center"
            })
            setload(true)
        } catch (error) {
            toast.error('Something is wrong', {
                position: "top-center"
            })
        }
    }











    const Isloading = () => {
        return (
            <>
                <div class="d-flex justify-content-center">
                    <div class="spinner-border text-primary" role="status">
                        <span class="visually-hidden">Loading...</span>
                    </div>
                </div>
            </>
        )
    }
    const Datanull = () => {
        return (
            <h1>Not Found</h1>
        )
    }

    return (
        <>
            {Loading ? (<Isloading />) : (<div>
                <Link to={`/home`} style={{textDecoration: 'none'}} activeClassName="current">
    <i className='fa fa-arrow-left' style={{fontSize:'30px'}}></i>
    </Link>

                <label htmlFor="" className='eblabel'>image</label><br />
                <img src={data.image} width="100px" className='ebimg' data-bs-toggle="modal"
                    data-bs-target="#exampleModal" /><hr />
                {/* https://bobbyhadz.com/blog/react-change-button-text-onclick */}

                <form >
                    <label htmlFor="" className='eblabel'>Title</label><br />
                    <input type="text" name="title" defaultValue={data.title} onChange={(e) => { settitle(e.target.value) }} id="ebinput" /><br /><button className='btn btn-success' id='ebbtn' onClick={() => titlesubmit()}>Save</button><hr />
                    <label htmlFor="" className='eblabel'>description</label><br />
                    <input type="text" name="description" defaultValue={data.description} onChange={(e) => { setdescription(e.target.value) }} id="ebinput" /><br /><button className='btn btn-success' id='ebbtn' onClick={() => dessubmit()}>Save</button><hr />

                </form>




                <div
                    className="modal fade"
                    id="exampleModal"
                    tabIndex={-1}
                    aria-labelledby="exampleModalLabel"
                    aria-hidden="true"
                >
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">
                                    upload Image
                                </h5>
                                <button
                                    type="button"
                                    className="btn-close"
                                    data-bs-dismiss="modal"
                                    aria-label="Close"
                                />
                            </div>
                            <div className="modal-body">
                                <img id='output' style={{ width: '100px' }} />
                                <input type="file" className='custom-file-input' name="image" onChange={(e) => {
                                    loadFile(e)
                                    setimguser(e.target.files[0])
                                }} id="" required /><br />


                            </div>
                            <div className="modal-footer">
                                <button
                                    type="button"
                                    className="btn btn-danger"
                                    data-bs-dismiss="modal"
                                >
                                    cancel
                                </button>
                                <button type="button" className="btn btn-success" onClick={okay} data-bs-dismiss="modal" aria-label="Close" >
                                    Upload
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            )}
            <ToastContainer />
        </>
    )
}

export default Editblog