import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate, Link } from "react-router-dom";
import '../style/userview.css';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

// const socket = io.connect('http://localhost:5500')
// var sockets = io('/user-namespace', {
//   auth: {
//     token: localStorage.getItem("id")
//   }
// });

const Searchengine = () => {
  const [data, setData] = useState([]);
  const [search, setsrch] = useState('')

  const [Loading, setloading] = useState(true)

  const navigate = useNavigate();


  useEffect(()=>{
    window.addEventListener("offline", (e) => {
      toast.info('you are offline.', {
        position: "bottom-center"
    })
    
    });
    
    window.addEventListener("online", (e) => {
      toast.info('you are online.', {
        position: "bottom-center"
    })

    });
    
  },[])

  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get('/dashboard')
        console.log(response.data.user);
        if (response.data.user === "error") {
          navigate('/', { replace: true })


        }

      } catch (error) {
        console.error(error);
      }

    }
    getUser()


  }


    , [])


  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get(`/searchusers`)
        // console.log(response.data.user);
        if (response.data.user === 'error') {
          navigate('/home', { replace: true })
        } else {
          setData(response.data.user)
          setloading(false)
        }
      } catch (error) {
        console.error(error);
      }

    }
    getUser()


  }


    , [])



  const searchusers = data.filter((item) => {
    if (search === "") {
      return item
    }
    else if (item.name.toLowerCase().includes(search.toLocaleLowerCase())) {
      return item
    }
  })

  return (
    <>
    <div className='searchuser' style={{backgroundColor:'white'}}>
    <Link to={`/home`} style={{textDecoration: 'none'}} activeClassName="current">
    <i className='fa fa-arrow-left' style={{fontSize:'30px'}}></i>
    </Link>

      <div class="input-group">
        <div class="form-outline">
          <input type="search" id="form1" class="form-control" name='search' onChange={(e) => { setsrch(e.target.value) }} placeholder='Username' style={{ width: '350px', marginLeft: '5px' }} required />
          <label class="form-label" for="form1">Search Username</label>
        </div>
      </div>
    

  <div className="container profile-page">
    <div className="row">
      <div className="col-xl-6 col-lg-7 col-md-12">
        <div className="card profile-header" style={{height:'143px'}}>
          <div className="body">
          {/* searchusers mein use karoge toh bas data ko change karke searchusers kar dena  */}
          {searchusers.map((value) => (
            <div className="row" key={value._id}>
              <div className="col-lg-4 col-md-4 col-12">
              
                <div className="profile-image float-md-right" style={{display:'flex',marginBottom:'20px'}}>
                {value.is_online === "1" ?  <Link to={`/profile/${value._id}`} style={{textDecoration: 'none'}} activeClassName="current">
                <img src={value.image} className='' loading='lazy'  width="4px" height="14px" style={{ border: "2px solid lightgreen",borderRadius:'50%',height:'44px', width:'44px' }} />

</Link> :  <Link to={`/profile/${value._id}`} style={{textDecoration: 'none'}} activeClassName="current">
                <img src={value.image} className=''  width="4px" height="14px" style={{ border: "2px solid grey",borderRadius:'50%',height:'44px', width:'44px' }} />

</Link>}

               

<div className="col-lg-8 col-md-8 col-12" style={{marginLeft:'12px',display:'flex'}}>
               
                <div style={{marginRight:'10px',marginLeft:'10px',display:'flex'}}>
                <h4 className="m-t-0 m-b-0">
                  <b>{value.name} {value.is_verified === '1'?value.blue_tick === '1'?<i className='fa fa-check-circle' style={{color:'blue'}}></i>:<i className='fa fa-check-circle'></i>:<i className='fa fa-times-circle' style={{color:'red'}}></i>}</b>
                </h4>

                </div>
              </div>
                </div>
               
              </div>
              
            </div>))}
          </div>
        </div>
      </div>
  </div>  
  </div>
  </div>
  <ToastContainer/>
    </>
  )
}

export default Searchengine