import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { useNavigate, Link } from "react-router-dom";
import jsPDF from 'jspdf'
import '../style/Downloadtextpost.css'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Downloadtpost = () => {
  const verifyid = useParams()
  const navigate = useNavigate();
  const [data, setData] = useState([])
  const [Loading, setloading] = useState(true)
  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get('/dashboard')

        if (response.data.user === "error") {
          navigate('/', { replace: true })


        }

      } catch (error) {
        console.error(error);
      }

    }
    getUser()


  }


    , [])
  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get(`/textspostview/${verifyid.id}`)

        setData(response.data.post)
        setloading(false)

      } catch (error) {
        toast.error('Something is Wrong!', {
          position: "bottom-center"
        })
      }

    }
    getUser()


  }


    , [])


  useEffect(() => {
    window.addEventListener("offline", (e) => {
      toast.info('you are offline.', {
        position: "bottom-center"
      })

    });

    window.addEventListener("online", (e) => {
      toast.info('you are online.', {
        position: "bottom-center"
      })

    });

  }, [])


  const generatePDF = () => {
    var docs = new jsPDF("p", "pt", "a4")
    docs.html(document.getElementById('container'), {
      callback: function (pdf) {
        let text = data.question;
        let result = text.slice(0, 10);
        toast.success('Pdf is Downloaded!', {
          position: "bottom-center"
        })
        pdf.save(`${result}.pdf`)
      }
    })
  }
  const Isloading = () => {
    return (
      <>
        <div class="d-flex justify-content-center">
          <div class="spinner-border text-primary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>
      </>
    )
  }


  return (
    <>
      {Loading ? <Isloading /> : (<>
        <Link to={`/home`} style={{ textDecoration: 'none' }} activeClassName="current">
          <i className='fa fa-arrow-left' style={{ fontSize: '30px' }}></i>
        </Link>
        <div className="alert alert-info" style={{marginLeft:'113px'}} onClick={generatePDF} role="alert">
          click here to download
        </div>

        <div id='container'>
          <div style={{ display: 'grid',justifyContent:"center" }}>
            <img src='https://res.cloudinary.com/arcloud555/image/upload/v1683465039/AR_Extrabook_1_mgtmty.png' className='logo' />
            <h4 className="font-weight-bolder">AR ExtraBook</h4>
          </div><br />

          <span>Question: <h2 style={{ color: "red" }}>{data.question}</h2></span>
          <hr />
          <span >Answer: <h3 style={{ color: "blue" }}>{data.answer}</h3></span>
          <br/>
          <span>Created by: <h6 style={{ color: "green" }}> {data.username}</h6></span>


        </div>
      </>)}
      <ToastContainer />
    </>

  )
}

export default Downloadtpost