import React, { Suspense, useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { useNavigate } from "react-router-dom";
import '../style/editprofile.css';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Editprofile = () => {



    const [data, setData] = useState({});
    const navigate = useNavigate();
    const verifyid = useParams('');
    const [imgurl, setimgurl] = useState('')
    const [Loading, setloading] = useState(true)
    const [Loadingimg, setloadingimg] = useState(false)
    const [load, setload] = useState(false)




    useEffect(() => {
        async function getUser() {
            try {
                const response = await axios.get('/dashboard')
                console.log(response.data.user);
                if (response.data.user === "error") {
                    navigate('/', { replace: true })

                }
                setData(response.data.user)
                setloading(false)
                setload(false)
            } catch (error) {
                toast.error('Something is wrong!', {
                    position: "top-center"})
                               navigate('/', { replace: true, })

            }

        }
        getUser()


    }


        , [load])
        useEffect(()=>{
            window.addEventListener("offline", (e) => {
              toast.info('you are offline.', {
                position: "bottom-center"
            })
            
            });
            
            window.addEventListener("online", (e) => {
              toast.info('you are online.', {
                position: "bottom-center"
            })
      
            });
            
          },[])
        const loadFile = function (e) {
            var reader = new FileReader();
            reader.onload = function () {
                var output = document.getElementById('output');
                output.src = reader.result;
            };
            reader.readAsDataURL(e.target.files[0]);
        };
        const upadteimag = async(imageurl) => {
            try {
                await axios.post(`/profileedit`, {
                    image: imageurl,
                    user_id: data._id
                    
                }
                )
            } catch (error) {
                toast.error('Something is wrong!', {
                    position: "top-center"})
                           }
        }
    const okay = async () => {
        toast.success('Image update Succesffuly', {
            position: "top-center"})

        try {
            
            setloadingimg(true)
            const data = new FormData();
            data.append("file", imguser)
            data.append("upload_preset", "Arinsta")
            data.append("cloud_name", "arcloud555")


            fetch('https://api.cloudinary.com/v1_1/arcloud555/image/upload', {
                method: "post",
                body: data,

            }).then((res) => res.json())
                .then(data => {
                 upadteimag(data.url)
                 setloadingimg(false)
                 setload(true)
                
                 
                })
                .catch(err => {
                    toast.error('Something is wrong!', {
                        position: "top-center"})
                   
                })

        } catch (error) {
            console.log(error)
        }
    }

    const [user, setuser] = useState('');
    const [email, setemail] = useState('');

    const [imguser, setimguser] = useState('');


const namesubmit  = async() => {
    toast.info('please wait updating your Username.', {
        position: "top-center"})
   
    try {
        
        await axios.post(`/profileedit`, {
            name: user,
            user_id: data._id

        }
        ).then((req) => {
            if (req.data.message === 'Already') {
                toast.success('this username is already exist.', {
                    position: "top-center"})
                            
            } else {
                toast.success('name update Successfully', {
                    position: "top-center"})
            setload(true)
            }
        })
       
    } catch (error) {
        toast.error('Something is wrong!', {
            position: "top-center"})
           }
}
const emailsubmit = async() => {
    toast.info('please wait updating your E-mail.', {
        position: "top-center"})
   
    try {
        
        await axios.post(`/profileedit`, {
            email: email,
            user_id: data._id

        }
        )
        setload(true)
        
    } catch (error) {
        toast.error('Something is wrong!', {
            position: "top-center"})
           }
}


    const Isloading = () => {
        return (
            <>
                <div class="d-flex justify-content-center">
                    <div class="spinner-border text-primary" role="status">
                        <span class="visually-hidden">Loading...</span>
                    </div>
                </div>
            </>
        )
    }
   


    return (
        <>
            {Loading?(<Isloading/>):Loadingimg?<Isloading/>:( <div> <img src={data.image} width="100px" className='epimg'  data-bs-toggle="modal"
                    data-bs-target="#exampleModal" />
                {/* <input type="file" name="image" onChange={(e) => { setimguser(e.target.files[0]) }} /><br />
                <input type="submit" onClick={okay} value="Update pic" id="" /> */}

                <form>
                    <label htmlFor="" className='eplabel'>Username</label><br/>
                    <input type="text" name="name" defaultValue={data.name} onChange={(e) => { setuser(e.target.value) }} id="epinput" />
                    <button className='btn btn-success' id='epbtn' onClick={namesubmit}>save</button><hr />
                    <label htmlFor="" className='eplabel'>Email</label><br/>
                    <input type="email" name="email" defaultValue={data.email} onChange={(e) => { setemail(e.target.value) }} id="epinput" /><button className='btn btn-success' id='epbtn' onClick={emailsubmit}>save</button><hr />
                                    </form>

                                    <div
                            className="modal fade"
                            id="exampleModal"
                            tabIndex={-1}
                            aria-labelledby="exampleModalLabel"
                            aria-hidden="true"
                        >
                            <div className="modal-dialog">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title" id="exampleModalLabel">
                                            upload Image
                                        </h5>
                                        <button
                                            type="button"
                                            className="btn-close"
                                            data-bs-dismiss="modal"
                                            aria-label="Close"
                                        />
                                    </div>
                                    <div className="modal-body">
                                        <img id='output' style={{ width: '100px' }} />
                                        <input type="file" className='custom-file-input' name="image" onChange={(e) => {
                                            loadFile(e)
                                            setimguser(e.target.files[0])
                                        }} id="" required /><br />


                                    </div>
                                    <div className="modal-footer">
                                        <button
                                            type="button"
                                            className="btn btn-danger"
                                            data-bs-dismiss="modal"
                                        >
                                            cancel
                                        </button>
                                        <button type="button" className="btn btn-success" onClick={okay} data-bs-dismiss="modal" aria-label="Close" >
                                            Upload
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                </div>
)}
<ToastContainer/>
        </>
    )
}

export default Editprofile