import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate, Link } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const Myblogs = () => {
  const [data, setData] = useState([]);
  const navigate = useNavigate();
  const [Loading, setloading] = useState(true)

  // login check karta hai
  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get('/dashboard')
        console.log(response.data.user);
        
        if (response.data.user === "error") {
          navigate('/', { replace: true })


        }

      } catch (error) {
        toast.error('Something is wrong', {
          position: "top-center"
      });
      }

    }
    getUser()


  }


    , [])
  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get(`/myblogs`)
        // console.log(response.data.post._id);
        setData(response.data.post)
        setloading(false)
      } catch (error) {
        toast.error('Something is wrong', {
          position: "top-center"
      });
      }

    }
    getUser()


  }


    , [])


  {/* yeh code direct connect hai ek button se */ }

  const deleteblog = async (blog) => {
    // console.log(blog)

    try {
      const response = await axios.get(`/mydeleteblogs/${blog}`)
      // console.log(response.data.post);
      // setData(response.data.post)
      toast.success('Deleted Successfully', {
        position: "top-center"
    });
    } catch (error) {
      toast.error('Something is wrong', {
        position: "top-center"
    });
    }


  }
  useEffect(()=>{
    window.addEventListener("offline", (e) => {
      toast.info('you are offline.', {
        position: "bottom-center"
    })
    
    });
    
    window.addEventListener("online", (e) => {
      toast.info('you are online.', {
        position: "bottom-center"
    })

    });
    
  },[])

  const Isloading = () => {
    return (
      <>
        <div class="d-flex justify-content-center">
          <div class="spinner-border text-primary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>
      </>
    )
  }
  const Datanull = () => {
    return (
      <h1>Not Found</h1>
    )
  }
  // console.log(data.length)
  const Data = () => {
    return (
      <div style={{ marginTop: '80px' }}>














        {data.length === 0 ? (<Datanull />) : (<>
          <div className="d-flex justify-content-between align-items-center mb-4">
         
          </div>
          <div className="row g-2">
            {data.map((value) => (
              <div className="col mb-2" key={value._id}>
                <Link to={`/blogview/${value._id}`} style={{textDecoration: 'none'}} activeClassName="current">

                  <img
                    src={value.image}
                    alt="image 1"
                    className="w-100 rounded-3"
                    style={{width:'200px', height:'150px'}}
                     loading='lazy'
                  />
                  </Link>

                  <i className='fa fa-trash' onClick={() => { deleteblog(value._id) }} style={{ marginRight: "20px", fontSize: "20px" }}></i>

                  <Link to={`/editblog/${value._id}`} style={{textDecoration: 'none'}} activeClassName="current"><i className='fa fa-pencil-square-o' style={{ fontSize: "20px" }}></i></Link>
              </div>))}

          </div>
        </>
        )}

















        {/* {data.length === 0 ?(<Datanull />):(
        <div className="App">
            {data.map((value) => (
              <div key={value._id}>
                <img src={value.image} width="100px"/>
                <h1>{value.title}</h1>
                <h1>{value.description}</h1>
    
                <Link to={`/editblog/${value._id}`} activeClassName="current">edit</Link>
                {/* <Link to={`/mydeleteblogs/${value._id}`} activeClassName="current">Delete</Link> */}

      </div>)

  }

  return (
    <>

      {Loading ? (<Isloading />) : (<Data />)}
<ToastContainer/>
    </>
  )
}

export default Myblogs