import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Forgetpassword = () => {
    const [password, setpassword] = useState('');


    var getdata = "Please Enter Email and password";

    const navigate = useNavigate();



    const [data, setData] = useState('');
    const [getdatas, getsetData] = useState('');

    const verifyid = useParams('');
    
    useEffect(()=>{
        window.addEventListener("offline", (e) => {
          toast.info('you are offline.', {
            position: "bottom-center"
        })
        
        });
        
        window.addEventListener("online", (e) => {
          toast.info('you are online.', {
            position: "bottom-center"
        })
    
        });
        
      },[])
    useEffect(() => {
        async function getUser() {
            try {
                const response = await axios.get(`/reset-password/${verifyid.token}`)
                console.log(response);
                setData(response.data.user_id)

            } catch (error) {
                toast.error('Something is wrong!', {
                    position: "top-center"})
               
            }

        }
        getUser()


    }


        , [])




    let submit = async (e) => {
        e.preventDefault()
        toast('Submited', {
            position: "top-center"})
            try {
            await axios.post(`/reset-password/${verifyid.token}`, {
                password: password,
                user_id: data

            }
            ).then(function (response) {
                getdata = response.data.message;
                if (getdata === "Successfull") {
                    toast.success('Forget Successfully', {
                        position: "top-center"})
                                    navigate('/home', { replace: true, })
                  
                } else {
                    console.log(response.data.message + " Email and Password")
                    getdata = response.data.message + " Email and Password";

                }

            })
                .catch(function (error) {
                    console.log(error);
                });
            getsetData(getdata);
            toast(getdata, {
                position: "bottom-center"})
        

        } catch (error) {
            toast.error('Something is wrong!', {
                position: "top-center"})
                   }

    }


    return (
        <>
            {/* <h1>Login</h1>
            <form action="post" method="post">
                <label htmlFor="">Password</label>
                <input type="password" onChange={(e) => { setpassword(e.target.value) }} name="password" required />
                <br /><br />
                <input type="submit" onClick={submit} value={'reset'} />
            </form>

 */}




            <form method='post'>
                <h1>Forget Password</h1>
                <div className="form-outline mb-4">
                    <label className="form-label" for="form2Example1">Password</label>
                    <input type="password" name='password' onChange={(e) => { setpassword(e.target.value) }} id="form2Example1" className="form-control" />

                </div>

                <button type="submit" onClick={submit} style={{ width: "358px" }} value={'Reset'} class="btn btn-primary btn-block mb-4">Reset</button>
            </form>

            {/* <h2 style={{ color: "red" }}>{getdatas}</h2> */}
            <ToastContainer/>
        </>
    )
}

export default Forgetpassword