import React, { useState, useEffect } from 'react'
import { useNavigate } from "react-router-dom";
// import {messaging} from './public/firebase';

import axios from 'axios';


import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import '../style/login.css'


const Login = () => {
  const [email, setemail] = useState('');
  const [password, setpassword] = useState('');
  
  const [data, setData] = useState('');



  const navigate = useNavigate();

  useEffect(()=>{
    window.addEventListener("offline", (e) => {
      toast.info('you are offline.', {
        position: "bottom-center"
    })
    
    });
    
    window.addEventListener("online", (e) => {
      toast.info('you are online.', {
        position: "bottom-center"
    })

    });
    
  },[])
  // async function requserpermission(){
  //   const permission = await Notification.requestPermission()
  //   if (permission === "granted") {

  //   } else {

  //   }
  // }



  // React.useEffect(()=>{
  //   const msg=firebase.messaging();
  //   msg.requestPermission().then(()=>{
  //     return msg.getToken();
  //   }).then((data)=>{
  //     console.warn("token",data)
  //   })
  // })


  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get('/dashboard')
        console.log(response.data.user);
        if (response.data.user === "error") {
          return

        } else {
          navigate('/home', { replace: true })

        }

      } catch (error) {
        console.log(error)
        toast.error(error, {
          position: "top-center"})
           }

    }
    getUser()


  }


    , [])






  var getdata = "Please Enter Email and password";

  let submit = async (e) => {
    e.preventDefault()
    toast('summited', {
      position: "top-center"})
    try {
      await axios.post('/login', {
        email: email,
        password: password

      }
      ).then(function (response) {
        getdata = response.data.message;
        if (getdata === "Successfull") {
          console.log(response.data.message)
          toast.success('Login Successfully!', {
            position: "top-center"})
            if (localStorage.getItem('url')) {
              navigate(localStorage.getItem('url'), { replace: true, })
            }else if(localStorage.getItem('Turl')){
              navigate(localStorage.getItem('Turl'), { replace: true, })
            }
            else{
            navigate('/home', { replace: true, })
            }
          

        } else {
          console.log(response.data.message + " Email and Password")
          getdata = response.data.message + " Email and Password";

        }

      })
        .catch(function (error) {
          toast.error(error, {
            position: "top-center"})
               });
      setData(getdata);
      toast.warn(getdata, {
        position: "top-center"})
     
    } catch (error) {
      console.log(error)
    }

  }


  return (
    <>


<div className='login'>




      <form method='post'>
        <h1>Login</h1>
        <div className="form-outline mb-4">
          <input type="email" onChange={(e) => { setemail(e.target.value) }} id="form2Example1" className="form-control" />
          <label className="form-label" for="form2Example1">Email address</label>
        </div>


        <div className="form-outline mb-4">
          <input type="password" onChange={(e) => { setpassword(e.target.value) }} id="form2Example2" className="form-control" />
          <label className="form-label" for="form2Example2">Password</label>
        </div>


        <div className="row mb-4">
          {/* <div className="col d-flex justify-content-center">
      
      <div className="form-check">
        <input className="form-check-input" type="checkbox" value="" id="form2Example31" checked />
        <label class="form-check-label" for="form2Example31"> Remember me </label>
      </div>
    </div> */}

          <div class="col">

            <a href="/forget" style={{color:'orangered',fontWeight:'bolder'}}>Forgot password?</a>
          </div><br />
          <div className='col'>
            <a href='/verify' style={{color:'orangered',fontWeight:'bolder'}} >Verification</a>

          </div>
        </div>


        <button type="submit" onClick={submit} value={'Login'} style={{width:"348px",marginLeft:'5px'}} class="btn btn-primary btn-block mb-4">Sign in</button>


        <div class="text-center">
          <p>Not a member? <a href="/resgister" style={{color:'orangered',fontWeight:'bolder'}}>Resgister</a></p>
          <p>or sign up with:</p>

        </div>
      </form></div>
      <a href='/auth/google'>
      <button className='btn btn-primary'>
        Login with google
      </button></a>
      <ToastContainer />

    </>
  )
}

export default Login