import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate, Link } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Searchblogs = () => {
    const [data, setData] = useState([]);
    const [search, setsrch] = useState('')

    const [Loading, setloading] = useState(true)
  
    const navigate = useNavigate();
  
    useEffect(() => {
        async function getUser() {
          try {
            const response = await axios.get('/dashboard')
            console.log(response.data.user);
            if (response.data.user === "error") {
              navigate('/', { replace: true })
    
    
            }
    
          } catch (error) {
            toast.error('Something is wrong', {
              position: "top-center"
          })          }
    
        }
        getUser()
    
    
      }
    
    
        , [])
    
        useEffect(() => {
          window.addEventListener("offline", (e) => {
            toast.info('you are offline.', {
              position: "bottom-center"
          })
          
          });
          
          window.addEventListener("online", (e) => {
            toast.info('you are online.', {
              position: "bottom-center"
          })
      
          });
          
        },[])

      useEffect(() => {
        async function getUser() {
          try {
            const response = await axios.get(`/searchblogs`)
            // console.log(response.data.user);
            if (response.data.blog === 'error') {
              navigate('/dashboard', { replace: true })
            } else {
              setData(response.data.blog)
              setloading(false)
            }
          } catch (error) {
            toast.error('Something is wrong', {
              position: "top-center"
          })          }
    
        }
        getUser()
    
    
      }
    
    
        , [])
    


    const searchblogs = data.filter((item) => {
        if (search === "") {
            return item
        }
        else if (item.title.toLowerCase().includes(search.toLocaleLowerCase())) {
            return item
        }
    })


    return (
        <>
        <div>
        <Link to={`/home`} style={{textDecoration: 'none'}} activeClassName="current">
    <i className='fa fa-arrow-left' style={{fontSize:'30px'}}></i>
    </Link>

        <label htmlFor="">Search Blog</label><br />
            <input type="text" name='search' onChange={(e) => { setsrch(e.target.value) }} placeholder='Username' required />
            <div>
                {searchblogs.map((blog) => (
                    <div className="alert alert-primary" role="alert">
                        <img src={blog.image} width="50px" style={{ borderRadius: "2px" }} />
                        <h6>{blog.title}</h6>
                        <Link to={`/blogview/${blog._id}`} style={{textDecoration: 'none'}} activeClassName="current"><b>View</b></Link>

                    </div>
                ))}
            </div>

        </div>
        <ToastContainer/>
        </>
    )
}

export default Searchblogs