import React, { useState,useEffect } from 'react'
import axios from 'axios';
import '../style/resgister.css';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useNavigate } from "react-router-dom";

const Resgister = () => {
    const [user, setuser] = useState('');
    const [email, setemail] = useState('');
    const [password, setpassword] = useState('');
    const [Loadingimg, setloadingimg] = useState(false)

    const [imguser, setimguser] = useState('');
    const [imgurl, setimgurl] = useState('')


    const navigate = useNavigate();


    const loadFile = function (e) {
        var reader = new FileReader();
        reader.onload = function () {
            var output = document.getElementById('output');
            output.src = reader.result;
        };
        reader.readAsDataURL(e.target.files[0]);
    };




    useEffect(()=>{
        window.addEventListener("offline", (e) => {
          toast.info('you are offline.', {
            position: "bottom-center"
        })
        
        });
        
        window.addEventListener("online", (e) => {
          toast.info('you are online.', {
            position: "bottom-center"
        })
    
        });
        
      },[])
      
    const okay = async () => {
        setloadingimg(true)
        try {
            const data = new FormData();
            data.append("file", imguser)
            data.append("upload_preset", "Arinsta")
            data.append("cloud_name", "arcloud555")


            fetch('https://api.cloudinary.com/v1_1/arcloud555/image/upload', {
                method: "post",
                body: data,

            }).then((res) => res.json())
                .then(data => {
                    setimgurl(data.url)
                    setloadingimg(false)
                    toast.success('Image upload Successfully', {
                        position: "top-center"})
                   
                })
                .catch(err => {
                    toast.error(err, {
                        position: "top-center"})
                   
                })

        } catch (error) {
            console.log(error)
        }
    }

    let submit = async (e) => {
        e.preventDefault()
       
        if (imgurl === "") {
            toast.info('Please upload your pic', {
                position: "top-center"})
           
        } else {
            toast.info('please wait for response...', {
                position: "top-center"})
                       // console.log(user)
            try {
                console.log(imgurl + "iifhrur")
                await axios.post('/signup', {
                    name: user,
                    email: email,
                    password: password,
                    image: imgurl

                }

                ).then((res)=>{
                   
                    if (res.data.message === 'Resgistration') {
                        navigate('/', { replace: true })

                    }else if(res.data.message === 'Already'){
                        toast.warn('email or username is already exist.', {
                            position: "top-center"})
            
                    }else if (res.data.message === 'unfilled'){
                        toast.warn('please fill all boxes.', {
                            position: "top-center"})
             
                    }else{
                        toast.success('please wait for response...', {
                            position: "top-center"})
                    }
                })
                // console.log(user)

            } catch (error) {
                toast.error(error, {
                    position: "top-center"})
               
            }
        }

    }
    const Isloading = () => {
        return (
            <>
                <div class="d-flex justify-content-center">
                    <div class="spinner-border text-primary" role="status">
                        <span class="visually-hidden">Loading...</span>
                    </div>
                </div>
            </>
        )
    }

    return (
        <>
            {Loadingimg ? <Isloading /> : (<>
                <h1 style={{ marginLeft: '80px' }}>AR ExtraBook</h1>
                <label htmlFor="" className='reslabel'>Image</label><br />

                <i
                    style={{ fontSize: '50px', marginLeft: '150px' }}

                    className="fa fa-file-image-o"
                    data-bs-toggle="modal"
                    data-bs-target="#exampleModal"
                >
                </i><hr />

                <form>
                    <label htmlFor="" className='reslabel'>Username</label><br />
                    <input type="text" name="name" onChange={(e) => { setuser(e.target.value) }} id="resinput" required /><br />
                    <hr />
                    <label htmlFor="" className='reslabel'>E-mail</label><br />

                    <input type="email" name="email" onChange={(e) => { setemail(e.target.value) }} id="resinput" required /><br />
                    {/* <input type="file" name="image" onChange={(e) => { setimguser(e.target.files[0]) }} id="" required /><br /> */}
                    <hr />
                    <label htmlFor="" className='reslabel'>Password</label><br />

                    <input type="password" name="password" onChange={(e) => { setpassword(e.target.value) }} id="resinput" style={{ borderTop: 'hidden', borderLeft: 'hidden', borderRight: 'hidden' }} required /><br />

                    <hr />
                    <input type="submit"  className='btn btn-primary' style={{ width: '200px', marginLeft: '80PX' }} onClick={submit} value="register" id="" />
                </form>





                <>
                    <>
                        {/* Button trigger modal */}

                        {/* Modal */}
                        <div
                            className="modal fade"
                            id="exampleModal"
                            tabIndex={-1}
                            aria-labelledby="exampleModalLabel"
                            aria-hidden="true"
                        >
                            <div className="modal-dialog">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title" id="exampleModalLabel">
                                            upload Image
                                        </h5>
                                        <button
                                            type="button"
                                            className="btn-close"
                                            data-bs-dismiss="modal"
                                            aria-label="Close"
                                        />
                                    </div>
                                    <div className="modal-body">
                                        <img id='output' style={{ width: '100px' }} />
                                        
                                        <input type="file" className='custom-file-input' name="image" onChange={(e) => {
                                            loadFile(e)
                                            setimguser(e.target.files[0])
                                        }} id="" required /><br />


                                    </div>
                                    <div className="modal-footer">
                                        <button
                                            type="button"
                                            className="btn btn-secondary"
                                            data-bs-dismiss="modal"
                                        >
                                            Close
                                        </button>
                                        <button type="button" className="btn btn-primary" onClick={okay} data-bs-dismiss="modal" aria-label="Close" >
                                            Upload
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </>

                </>
            </>)}
            <ToastContainer/>
        </>
    )
}

export default Resgister