import React, { useState } from 'react';
import axios from 'axios';
import './chatbot.css';
import Speech from 'react-text-to-speech'

const ChatbotComponent = () => {
  const [messages, setMessages] = useState('');
  const [sentence, setsentence] = useState('');
  // const convert = useTextToSpeech();



  const myJson = {
    "word": [{ "responses": "example" }],
    "hi": [{ "responses": "hello sir!" }],
    "good morning": [{ "responses": "good morning sir!" }],
    "good night": [{ "responses": `good night sir!` }]

  };

  const startBtn = <i className="fa fa-volume-off" style={{ fontSize: '25px', marginLeft: '20px' }} aria-hidden="true"></i>

  const stopBtn = <i className="fa fa-volume-up" style={{ fontSize: '25px', marginLeft: '20px' }} aria-hidden="true"></i>



  const handleOnSubmit = async () => {
    let chatbox = document.getElementsByClassName('chatbox');
    let mychat = document.createElement('div');

    try {
      // const myObj = JSON.parse(myJson);
      // [let results = myObj[input];]  = isme joh bhi input dala hoga vah json main jakar check hoga ki usme json main input joh dala hai ki nahi vah check hoga aur result namak variable main store hoga.
      let results = myJson[messages];
      let html = "";
      console.log(results)
      if (results !== undefined) {


        // yeh json ki reponses ko html namak variable store hoga
        results.forEach(element => {
          html += element.responses;
        });
        mychat.className = 'alert alert-warning';
        mychat.innerHTML = `<p>You: ${messages}</p>`;
        // convert(messages)
        chatbox[0].appendChild(mychat);
        let chat = document.createElement('div');
        // convert(html)
        setsentence(html)

        chat.innerHTML = `<p>Alex(bot): ${html}</p>`;
        chat.className = 'alert alert-success';
        chatbox[0].appendChild(chat);

        console.log(html)
      } else {
        let question = messages.slice(0, 4);
        let result = messages.slice(7);
        // agar question variable equal hai what se toh result ko dena magar nahi hai toh message diract dena 
        let query = question === "what" ? result : messages;
        console.log(query)
        // Call the Wikipedia API to get the answer
        const response = await axios.get(
          `https://en.wikipedia.org/api/rest_v1/page/summary/${query}`
        );

        const answer = response.data.extract;
        setsentence(answer)
        mychat.className = 'alert alert-warning';
        mychat.innerHTML = `<p>You: ${messages}</p>`;
        // convert(messages)
        chatbox[0].appendChild(mychat);
        let chat = document.createElement('div');
        // convert(answer,1)
        chat.innerHTML = `<p>Alex(bot): ${answer}</p>`;
        chat.className = 'alert alert-success';
        chatbox[0].appendChild(chat);
      }
    } catch (err) {
      mychat.className = 'alert alert-warning';
      mychat.innerHTML = `<p>You: ${messages}</p>`;
      // convert(messages)
      chatbox[0].appendChild(mychat);
      let chat = document.createElement('div');
      // convert(answer,1)
      chat.innerHTML = `<p>Alex(bot): please type directly query like per capita income.</p>`;
      chat.className = 'alert alert-success';
      chatbox[0].appendChild(chat);

    }

  };

  // Get the chat bot button and window elements
  // const chatbotWindow = document.getElementById('chatbot-window');

  // // Toggle the visibility of the chat bot window when the button is clicked
  // const chatbotButton = () => {
  //   chatbotWindow.classList.toggle('show');
  // }


  return (
    <div>



      <div id="chatbot-container">
        {/* Chat bot button */}
        <i className="fa fa-simplybuilt" aria-hidden="true" id="chatbot-button" data-bs-toggle="modal" data-bs-target="#chatbot"></i>


        {/* Chat bot window */}
        <>
          <div className="modal fade" id="chatbot" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="staticBackdropLabel" aria-hidden="true">
            <div className="modal-dialog">
              <div className="modal-content">
                <div className="modal-header">
                  <h1 className="modal-title fs-5" id="staticBackdropLabel">Chat bot</h1>
                  <Speech text={sentence} startBtn={startBtn} stopBtn={stopBtn} />

                  <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
                </div>
                <div className="modal-body">
                  <div className='chatbox'>

                  </div>

                </div>
                <div class="modal-footer">
                  <input type='text' id='chatbot-input' onChange={e => setMessages(e.target.value)} />


                  <button className="btn btn-primary" onClick={handleOnSubmit}>Submit</button>
                </div>
              </div>
            </div>
          </div>
          {/* Modal */}
        </>

      </div>





    </div>
  );
};

export default ChatbotComponent;
