import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { useNavigate, Link } from "react-router-dom";
import { FacebookShareButton, FacebookIcon, WhatsappShareButton, WhatsappIcon, TelegramShareButton, TelegramIcon, TwitterIcon, TwitterShareButton, FacebookMessengerShareButton, FacebookMessengerIcon, LineShareButton, LinkedinIcon, PinterestIcon, PinterestShareButton, LinkedinShareButton } from 'react-share'
import Download from './download';
import '../style/profile.css';
import '../style/blogview.css';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Speech from 'react-text-to-speech'

const Blogview = () => {


  // const [data, setData] = useState('');
  const [user, setuser] = useState(false)
  const [comment, setComment] = useState('');
  const [commentdata, setcommentdata] = useState([]);
  const [data, setData] = useState([]);
  const [cblogid, setblogid] = useState(false);
  const [likeid, setlike] = useState('')
  const [Loading, setloading] = useState(true)
  const [sharedata, setsharedata] = useState('')
  const [creator, setcreator] = useState([])

  const startBtn = <i className="fa fa-volume-off" style={{ fontSize: '25px', marginLeft: '20px' }} aria-hidden="true"></i>

  const stopBtn = <i className="fa fa-volume-up" style={{ fontSize: '25px', marginLeft: '20px' }} aria-hidden="true"></i>

  // const [Loading, setloading] = useState(true)
  const navigate = useNavigate();

  const verifyid = useParams('');


  useEffect(() => {
    window.addEventListener("offline", (e) => {
      toast.info('you are offline.', {
        position: "bottom-center"
      })

    });

    window.addEventListener("online", (e) => {
      toast.info('you are online.', {
        position: "bottom-center"
      })

    });

  }, [])

  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get('/dashboard')
        console.log(response.data.user);
        if (response.data.user === "error") {

          let url = localStorage.setItem('url', `/blogview/${verifyid.id}`)
          navigate('/', { replace: true })


        } else {
          localStorage.removeItem('url')
        }



      } catch (error) {
        toast.error('Something is wrong', {
          position: "top-center"
        })
      }

    }
    getUser()


  }


    , [])

  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get(`/blogsview/${verifyid.id}`)
        console.log(response.data.post._id);
        setData(response.data.post)
        setloading(false)
        setlike(response.data.userli)
        setcreator(response.data.creator)
        localStorage.setItem("id", response.data.userli)
        console.log(likeid + " fghj")
        setblogid(false)
      } catch (error) {
        toast.error('Something is wrong', {
          position: "top-center"
        })
      }

    }
    getUser()


  }


    , [cblogid])

  useEffect(() => {

    async function getComment() {
      try {
        const response = await axios.get(`/comment/${verifyid.id}`)
        console.log(response.data.usercomment);
        setcommentdata(response.data.usercomment)
        setuser(false)
      } catch (error) {
        toast.error('Something is wrong', {
          position: "top-center"
        })
      }

    }
    getComment()

  }, [user])
  let commentsub = async (blogid) => {
    console.log(blogid + "AA")
    if (blogid === undefined) {
      toast.error('Something is wrong', {
        position: "top-center"
      })
    } else if (comment === '') {
      toast.warn('plaese type comment', {
        position: "top-center"
      })
    } else {
      try {

        await axios.post(`/comment/${blogid}`, {
          content: comment
        }

        ).then((res) => {
          setuser(true)
          toast('Comment submit sucessfully', {
            position: "bottom-center"
          })

        })

      } catch (error) {
        toast.error('Something is wrong', {
          position: "top-center"
        })
      }
    }
  }


  const likeblog = async (userid) => {
    const response = await axios.get(`/like/${userid}`)


    console.log(response.data.user)
    if (response.data.user === "Like") {
      localStorage.setItem("id", likeid)
      // navigate('/blogs', { replace: true })
      setblogid(true)

      toast('Like blog', {
        position: "top-center"
      })
    } else {
      toast.error('Something is wrong', {
        position: "top-center"
      })
    }

  }
  const unlikeblog = async (userid) => {
    const response = await axios.get(`/unlike/${userid}`)


    console.log(response.data.user)
    if (response.data.user === "unlike") {
      toast('Unlike blog', {
        position: "top-center"
      })
      localStorage.setItem("id", "vdfvdfdf")
      // navigate('/blogs', { replace: true })
      setblogid(true)


    } else {
      toast.error('Something is wrong', {
        position: "top-center"
      })
    }

  }
  // const copyurl = (blogurl) => {
  //   navigator.clipboard.writeText(blogurl)
  //   toast.success('Copied!', {
  //     position: "top-center"})


  // }
  const Isloading = () => {
    return (
      <>
        <div class="d-flex justify-content-center">
          <div class="spinner-border text-primary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>
      </>
    )
  }
  const sharepost = (blogid) => {
    setsharedata(blogid)
  }
  const Data = () => {
    return (
      <>
      </>
    )
  }
  return (
    <>
      {Loading ? <Isloading /> : (
        <div className='blogview'>
          <Link to={`/home`} style={{ textDecoration: 'none' }} activeClassName="current">
            <i className='fa fa-arrow-left' style={{ fontSize: '30px' }}></i>
          </Link>

          <img src={data.image} width="365px" />
          <div style={{ marginLeft: '25px' }}>
            <div style={{ display: 'flex' }}>

              <h1>{data.title}</h1>
              <Speech text={data.description} startBtn={startBtn} stopBtn={stopBtn} />
            </div>
            <details style={{ backgroundColor: 'lightgreen', width: '320px', borderRadius: '2px' }}>
              <summary>Description</summary>
              <b style={{ wordWrap: 'break-word' }}>{data.description}</b>
            </details>
            <Link to={`/profile/${creator._id}`} style={{ textDecoration: 'none' }} activeClassName="current">

              <img src={creator.image} className='rounded-circle' width="44px" height="44px" style={{ border: "2px solid #00c4f1" }} /></Link>
            {/* 
            {creator.is_online === "1" ?  <Link to={`/profile/${creator._id}`} style={{textDecoration: 'none'}} activeClassName="current">
                <img src={creator.image} className=''  width="4px" height="194px" style={{ border: "2px solid lightgreen",borderRadius:'50%' }} />

</Link> :  <Link to={`/profile/${creator._id}`} style={{textDecoration: 'none'}} activeClassName="current">
                <img src={creator.image} className=''  width="4px" height="194px" style={{ border: "2px solid grey",borderRadius:'50%' }} />

</Link>} */}


            <h6>Created by {creator.name} {creator.is_verified === '1' ? creator.blue_tick === '1' ? <i className='fa fa-check-circle' style={{ color: 'blue' }}></i> : <i className='fa fa-check-circle'></i> : <i className='fa fa-times-circle' style={{ color: 'red' }}></i>}</h6>
          </div>

          <div>

            <div style={{ display: 'flex' }}>
              <div style={{ marginLeft: '25px' }}>
                {<div onClick={() => {
                  console.log(localStorage.getItem("id") + " fghj")
                  if (data.like.includes(localStorage.getItem("id"))) {
                    unlikeblog(data._id)

                  } else {
                    likeblog(data._id)
                    console.log("pass")

                  }
                }}>{data.like.includes(localStorage.getItem("id")) ? (<i className='fa fa-heart' style={{ color: "red", fontSize: "25px" }}></i>) : (<i className='fa fa-heart-o' style={{ fontSize: "25px" }}></i>)}</div>}

                <p>{data.like ? data.like.length : "0"} Likes</p>
              </div>
              <div data-bs-toggle="modal" data-bs-target="#share" style={{ marginLeft: '25px' }} onClick={() => { sharepost(data._id) }}>
                <i className='fa fa-share' style={{ fontSize: "30px" }}></i>
              </div>

              <Link to={`/download/${verifyid.id}`} style={{ textDecoration: 'none', marginLeft: '25px' }} activeClassName="current"><i class="fa fa-download" style={{ fontSize: '25px' }} aria-hidden="true"></i></Link>
            </div>
            <div style={{ display: 'flex' }}>
              <input type='text' name='content' className='textinp' placeholder='type commet' onChange={(e) => { setComment(e.target.value) }} />
              <i onClick={() => { commentsub(data._id) }} className="fa fa-paper-plane-o" style={{ fontSize: '25px' }}></i>
            </div>
            <hr />
            <div className="CApp">
              <h1>Comments <span style={{ fontWeight: 'bold' }}>{commentdata.length}</span></h1>
              {commentdata.map((comment) => (
                <div key={comment._id}>
                  <div style={{ backgroundColor: 'white' }}>
                    <div style={{ display: 'flex' }}>
                      <Link to={`/profile/${comment.userid}`} style={{ textDecoration: 'none', marginLeft: '25px' }} activeClassName="current">
                        <img src={comment.userimage} className='rounded-circle' width="60px" height="60px" style={{ border: "2px solid #00c4f1" }} />
                      </Link>
                      <h3 style={{ marginLeft: '10px', fontWeight: 'bolder' }}>{comment.username}</h3>
                    </div>
                    <h5 style={{ marginLeft: '10px' }}>{comment.content}</h5>
                  </div>
                  {/* <Link to={`/blogviews/${value._id}`} activeClassName="current"><h5>View</h5></Link> */}
                  {/* yeh code direct connect hai ek funtion se */}


                </div>

              ))}

            </div>
          </div>
        </div>



      )}
      <div className="modal fade" id="share" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h1 className="modal-title fs-5" id="staticBackdropLabel">Share Box</h1>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
            </div>
            <div className="modal-body">
              <FacebookShareButton url={`https://x6v4mq-5500.csb.app/blogview/${sharedata}`} quote='Blog' hashtag='#AR'>
                <FacebookIcon size={40} />
              </FacebookShareButton>
              <WhatsappShareButton url={`https://x6v4mq-5500.csb.app/blogview/${sharedata}`} quote='Blog' hashtag='#AR'>
                <WhatsappIcon size={40} />
              </WhatsappShareButton>
              <TelegramShareButton url={`https://x6v4mq-5500.csb.app/blogview/${sharedata}`} quote='Blog' hashtag='#AR'>
                <TelegramIcon size={40} />
              </TelegramShareButton>
              <PinterestShareButton url={`https://x6v4mq-5500.csb.app/blogview/${sharedata}`} quote='Blog' hashtag='#AR'>
                <PinterestIcon size={40} />
              </PinterestShareButton>
              <TwitterShareButton url={`https://x6v4mq-5500.csb.app/blogview/${sharedata}`} quote='Blog' hashtag='#AR'>
                <TwitterIcon size={40} />
              </TwitterShareButton>
              <FacebookMessengerShareButton url={`https://x6v4mq-5500.csb.app/blogview/${sharedata}`} quote='Blog' hashtag='#AR'>
                <FacebookMessengerIcon size={40} />
              </FacebookMessengerShareButton>
              <LinkedinShareButton url={`https://x6v4mq-5500.csb.app/blogview/${sharedata}`} quote='Blog' hashtag='#AR'>
                <LinkedinIcon size={40} />
              </LinkedinShareButton>
              {/* <i className='fa fa-clone' style={{fontSize:'40px'}} onClick={()=>{copyurl(`http://localhost:3000/blogview/${sharedata}`)}}></i> */}



            </div>
            {/* <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" class="btn btn-primary">Save changes</button>
              </div> */}
          </div>
        </div>
      </div>
      <ToastContainer />
    </>
  )
}

export default Blogview