import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Forget = () => {
  const [email, setemail] = useState('');

  const [data, setData] = useState('');


  const navigate = useNavigate();

  useEffect(()=>{
    window.addEventListener("offline", (e) => {
      toast.info('you are offline.', {
        position: "bottom-center"
    })
    
    });
    
    window.addEventListener("online", (e) => {
      toast.info('you are online.', {
        position: "bottom-center"
    })

    });
    
  },[])
  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get('/dashboard')
        console.log(response.data.user);
        if (response.data.user === "error") {
          return

        } else {
          navigate('/home', { replace: true })

        }

      } catch (error) {
        toast.error('Something is wrong!', {
          position: "top-center"})
           }

    }
    getUser()


  }


    , [])






  var getdata = "Please Enter Email and password";

  let submit = async (e) => {
    e.preventDefault()
    toast('submited', {
      position: "top-center"})
     try {
      await axios.post('/reset-password', {
        email: email,

      }
      ).then(function (response) {
        getdata = response.data.message;
        setData(getdata);
        toast.success(getdata, {
          position: "top-center"})
     

      })
        .catch(function (error) {
          toast.error('Something is wrong!', {
            position: "top-center"})
               });

    } catch (error) {
      toast.error('Something is wrong!', {
        position: "top-center"})
       }

  }


  return (
    <>
      {/* <div>
        <h1>Type your email</h1>
        <form action="post" method="post">
          <label htmlFor="">Email</label>
          <input type="email" onChange={(e) => { setemail(e.target.value) }} name="email" required />
          <br /><br />
          <input type="submit" onClick={submit} value={'reset'} />
        </form>
        <h2>{data}</h2>

      </div>
 */}




      <form method='post'>
        <h1>Type your email</h1>
        <div className="form-outline mb-4">
          <label className="form-label" for="form2Example1">E-mail</label>
          <input type="email" name='email' onChange={(e) => { setemail(e.target.value) }} id="form2Example1" className="form-control" />

        </div>

        <button type="submit" onClick={submit} style={{ width: "358px" }} value={'Reset'} class="btn btn-primary btn-block mb-4">Reset</button>
      </form>
      <h2>{data}</h2>


<ToastContainer/>

    </>
  )
}

export default Forget